

























































































/**
 *
 * @author fqq
 */
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";

import Url from "../../../api";

@Component({
  components: {}
})
export default class AuthTeam extends Vue {
  @Prop({ type: String, default: "" })
  id?: string;

  @Prop({ type: Boolean, default: false })
  dialogVisible!: boolean;

  teamDialog = false;
  loading = false;
  name = "";
  tableData = [];
  pageNum = 1;
  pageSize = 10;
  total = 0;

  addDialog = false;
  addLoading = false;
  teamName = "";
  teamData = [];
  teamPageNum = 1;
  teamPageSize = 10;
  teamTotal = 0;
  selection: Array<any> = [];

  @Watch("dialogVisible")
  handleInfo(newValue: boolean) {
    this.teamDialog = newValue;
    if (newValue) {
      this.name = "";
      this.pageNum = 1;
      this.pageSize = 10;
      this.getTableData();
    }
  }

  search() {
    this.pageNum = 1;
    this.getTableData();
  }

  getTableData() {
    this.loading = true;
    this.$http
      .post(Url.getTeamList + "/" + this.id, {
        query: {
          name: this.name
        },
        current: this.pageNum,
        size: this.pageSize
      })
      .then((res: any) => {
        this.loading = false;
        if (res.data.status) {
          this.tableData = res.data.data.records;
          this.total = res.data.data.total;
        } else {
          this.$message.error(res.data.message);
        }
      })
      .catch(() => {
        this.loading = false;
      });
  }

  handleCurrentChange(val: number) {
    this.pageNum = val;
    this.getTableData();
  }
  handleSizeChange(val: number) {
    this.pageSize = val;
    this.getTableData();
  }

  handleAdd() {
    this.teamName = "";
    this.teamPageNum = 1;
    this.teamPageSize = 10;
    this.selection = [];
    this.getTeamTableData();
    this.addDialog = true;
  }

  handleDelete(item: any) {
    let _this = this;
    this.$confirm("确认删除？", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        this.$http.get(Url.deleteAuthTeam + "/" + item.id).then((res: any) => {
          if (res.status) {
            if (res.data.status) {
              this.$message({
                type: "success",
                message: "删除成功!"
              });
              //重新获取列表
              _this.getTableData();
            } else {
              _this.$message({
                showClose: true,
                message: res.data.message,
                type: "error"
              });
            }
          }
        });
      })
      .catch(() => {});
  }

  teamSearch() {
    this.teamPageNum = 1;
    this.getTeamTableData();
  }

  // 获取未授权团队列表
  getTeamTableData() {
    this.addLoading = true;
    this.$http
      .post(Url.getNotAuthTeamData + "/" + this.id, {
        query: {
          name: this.teamName
        },
        current: this.teamPageNum,
        size: this.teamPageSize
      })
      .then((res: any) => {
        this.addLoading = false;
        if (res.data.status) {
          this.teamData = res.data.data.records;
          this.teamTotal = res.data.data.total;
        } else {
          this.$message.error(res.data.message);
        }
      })
      .catch(() => {
        this.addLoading = false;
      });
  }

  handleTeamCurrentChange(val: number) {
    this.teamPageNum = val;
    this.getTeamTableData();
  }
  handleTeamSizeChange(val: number) {
    this.teamPageSize = val;
    this.getTeamTableData();
  }

  handleSelectionChange(val: Array<any>) {
    this.selection = val.map((li: any) => li.tenantId);
  }

  handleAddTeam() {
    if (this.selection.length === 0) {
      this.$message.info("请先勾选团队");
      return;
    }
    this.addLoading = true;
    this.$http
      .post(Url.addAuthTeam + "/" + this.id, this.selection)
      .then((res: any) => {
        this.addLoading = false;
        if (res.data.status) {
          this.$message.success("添加成功");
          this.addDialog = false;
          this.getTableData();
        } else {
          this.$message.error(res.data.message);
        }
      })
      .catch(() => {
        this.addLoading = false;
      });
  }

  closeDialog() {
    this.$emit("close");
  }

  closeTeamDialog() {
    this.addDialog = false;
  }
}
