















/**
 *
 * @author
 */
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";

import Url from "../../../api";

@Component({
  components: {}
})
export default class BaseType extends Vue {
  @Prop({ type: String, default: "" })
  id?: string;

  @Prop({ type: Boolean, default: false })
  dialogVisible!: boolean;

  addDialog = false;
  loading = false;
  typeArray: Array<any> = [];
  checkAllType: boolean = false;
  isIndeterminate: boolean = true;
  checkBases: Array<any> = [];
  allBaseType: Array<any> = [];

  @Watch("dialogVisible")
  handleInfo(newValue: boolean) {
    this.addDialog = newValue;
    if (newValue) {
      this.allBaseType = [];
      this.checkBases = [];
      this.getBaseType();
    }
  }

  // 全选
  handleCheckAllChange(val: boolean) {
    this.checkBases = val ? this.allBaseType.map((li: any) => li.id) : [];
    this.isIndeterminate = false;
  }

  handleCheckedTypeChange(value: Array<any>) {
    let checkedCount = value.length;
    this.checkAllType = checkedCount === this.allBaseType.length;
    this.isIndeterminate = checkedCount > 0 && checkedCount < this.allBaseType.length;
  }

  handleSave() {
    this.loading = true;
    let ids = [];
    if (this.checkAllType) {
      ids = this.allBaseType.map((li: any) => li.id);
    } else {
      ids = this.checkBases;
    }
    this.$http.post(Url.savePackageBaseType + "/" + this.id, ids).then((res: any) => {
      this.loading = false;
      if (res.status) {
        if (res.data.status) {
          this.$message({
            type: "success",
            message: "基础属性设置成功"
          });
          this.closeDialog();
        } else {
          this.$message(res.data.message);
        }
      }
    });
  }

  closeDialog() {
    this.$emit("close");
  }

  getBaseType() {
    this.$http.get(Url.getPackageBaseType + "/" + this.id).then((res: any) => {
      if (res.data.status) {
        let data = res.data.data;
        this.allBaseType = data.allBaseType;
        this.checkBases = data.packageBaseType.map((li: any) => li.id);
      } else {
        this.$message.error(res.data.message);
      }
    });
  }
}
