























































































/**
 *
 * @author fqq
 */
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import AddPackage from "./components/AddPackage.vue";
import PackageInfo from "./components/PackageInfo.vue";
import BaseType from "./components/BaseType.vue";
import AuthTeam from "./components/AuthTeam.vue";
import FuncConfig from "./components/FuncConfig.vue";

import Url from "../../api";
import utils from "@/utils/utils";

@Component({
  components: {
    AddPackage,
    PackageInfo,
    BaseType,
    AuthTeam,
    FuncConfig
  }
})
export default class PackageManage extends Vue {
  tableForm = {
    name: ""
  };
  loading = false;
  tableData = [];
  total = 0;
  pageNum = 1;
  pageSize = 10;

  dialogTitle = "";
  visibleDialog = false;
  formData = {
    id: "",
    name: "",
    remark: ""
  };

  currentId = "";
  infoDialog = false;
  baseTypeDialog = false;
  authTeamDialog = false;
  funcConfigDialog = false;

  getTableData() {
    this.loading = true;
    this.$http
      .post(Url.getPackageList, {
        query: this.tableForm,
        current: this.pageNum,
        size: this.pageSize
      })
      .then((res: any) => {
        this.loading = false;
        if (res.data.status) {
          this.tableData = res.data.data.records;
          this.total = res.data.data.total;
        } else {
          this.$message.error(res.data.message);
        }
      })
      .catch(() => {
        this.loading = false;
      });
  }
  search() {
    this.pageNum = 1;
    this.getTableData();
  }

  handleCurrentChange(val: number) {
    this.pageNum = val;
    this.getTableData();
  }
  handleSizeChange(val: number) {
    this.pageSize = val;
    this.getTableData();
  }

  handleAdd() {
    this.dialogTitle = "添加套餐";
    this.formData = {
      id: "",
      name: "",
      remark: ""
    };
    this.visibleDialog = true;
  }
  handleEdit(row: any) {
    this.dialogTitle = "编辑套餐";
    utils.mapValue(this.formData, row);
    this.visibleDialog = true;
  }

  closeDialog() {
    this.visibleDialog = false;
  }

  handleAddSubmit() {
    this.search();
    this.visibleDialog = false;
  }

  handleDelete(item: any) {
    let _this = this;
    this.$confirm("确认删除？", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        this.$http.get(Url.deletePackage + "/" + item.id).then((res: any) => {
          if (res.status) {
            if (res.data.status) {
              this.$message({
                type: "success",
                message: "删除成功!"
              });
              //重新获取列表
              _this.getTableData();
            } else {
              _this.$message({
                showClose: true,
                message: res.data.message,
                type: "error"
              });
            }
          }
        });
      })
      .catch(() => {});
  }

  // 查看详细信息
  handleInfo(row: any) {
    this.currentId = row.id;
    this.infoDialog = true;
  }

  closeInfoDialog() {
    this.infoDialog = false;
  }

  // 基础属性
  handleBaseEdit(row: any) {
    this.currentId = row.id;
    this.baseTypeDialog = true;
  }
  closeBaseTypeDialog() {
    this.baseTypeDialog = false;
  }

  // 授权团队
  handleAddTeam(row: any) {
    this.currentId = row.id;
    this.authTeamDialog = true;
  }
  closeAuthTeamDialog() {
    this.authTeamDialog = false;
  }

  // 功能配置
  handleFuncConfig(row: any) {
    this.currentId = row.id;
    this.funcConfigDialog = true;
  }
  closeFuncConfigDialog() {
    this.funcConfigDialog = false;
  }

  created() {
    this.getTableData();
  }
}
