




































/**
 *
 * @author fqq
 */
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";

import Url from "../../../api";

@Component({
  components: {}
})
export default class App extends Vue {
  @Prop({ type: String, default: "" })
  id?: string;

  @Prop({ type: Boolean, default: false })
  dialogVisible!: boolean;

  @Watch("dialogVisible")
  async handleInfo(newValue: boolean) {
    this.addDialog = newValue;
    if (newValue) {
      this.treeData = [];
      this.navIdList = [];
      this.treeIdMap = {};
      this.$nextTick(() => {
        let tree: any = this.$refs.scopedRef;
        this.styleScopedId = Object.keys(tree.dataset).find((d: string) => d.substring(0, 2) === "v-");
      });
      this.loading = true;
      try {
        await Promise.all([this.getTreeData(), this.getFuncConfig()]);
      } finally {
        this.loading = false;
      }
    }
  }

  addDialog = false;
  loading: boolean = false;
  treeData = [];
  navIdList: Array<any> = [];
  defaultProps = {
    children: "children",
    label: "name"
  };
  packOperationMap: any = {};
  styleScopedId: string | undefined = "";
  // 用于保存选中数据 id -> ids[{}]
  treeIdMap: any = {};

  async getTreeData() {
    let {
      data: { status, data, message }
    } = await this.$http.get(Url.getFuncConfig);
    if (status) {
      this.treeData = data;
    } else {
      this.$message.error(message);
    }
  }

  async getFuncConfig() {
    let {
      data: { status, data, message }
    } = await this.$http.get(Url.getHasFuncConfig + "/" + this.id);
    if (status) {
      this.navIdList = data.navigationIds;
      this.packOperationMap = {};
      data.navigationOperationIds.forEach((id: string) => {
        this.packOperationMap[id] = true;
      });
    } else {
      this.$message.error(message);
    }
  }

  handleCheck(data: any, checked: boolean) {
    this.treeIdMap[data.id].forEach((li: any) => {
      li.checked = checked;
    });
  }

  renderContent(h: any, { node, data, store }: { node: any; data: any; store: any }) {
    // 给每一个节点添加当前组件的样式作用域hash值 data-v-xxxx
    const assignScopedId = (obj: any) => {
      if (this.styleScopedId) {
        obj.attrs = obj.attrs || {};
        obj.attrs["data-" + this.styleScopedId] = true;
      }
      return obj;
    };
    if (!this.treeIdMap[data.id]) {
      this.treeIdMap[data.id] = data.oparations.map((item: any) => {
        return {
          id: item.id,
          checked: this.packOperationMap[item.id]
        };
      });
    }
    return h(
      "div",
      assignScopedId({
        class: "tree-item"
      }),
      [
        h(
          "div",
          assignScopedId({
            class: "operate-left"
          }),
          data.name
        ),
        h(
          "div",
          assignScopedId({
            class: "operate-right"
          }),
          data.oparations.map((item: any, index: number) => {
            return h(
              "div",
              assignScopedId({
                class: "item",
                props: {
                  key: index
                }
              }),
              [
                h("el-checkbox", {
                  style: { marginRight: "8px" },
                  props: {
                    disabled: !node.checked,
                    checked: this.treeIdMap[data.id][index].checked,
                    value: this.treeIdMap[data.id][index].checked
                  },
                  on: {
                    input: (val: boolean) => {
                      this.treeIdMap[data.id][index].checked = val;
                    }
                  }
                }),
                item.operationName
              ]
            );
          })
        )
      ]
    );
  }

  async handleSave() {
    this.loading = true;
    let checkedNodeIdMap: any = {};
    let tree: any = this.$refs.tree;
    tree.getCheckedNodes().forEach((item: any) => {
      checkedNodeIdMap[item.id] = true;
    });
    let requestBody = Object.keys(this.treeIdMap)
      .filter((id: string) => checkedNodeIdMap[id])
      .map((id: any) => {
        return {
          navigationId: id,
          operationIds: this.treeIdMap[id].filter((d: any) => d.checked).map((d: any) => d.id)
        };
      });
    let navIds = [];
    let operateIds: Array<any> = [];
    for (let i in requestBody) {
      navIds.push(requestBody[i].navigationId);
      operateIds = operateIds.concat(requestBody[i].operationIds);
    }
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(Url.saveFuncConfig + "/" + this.id, {
        navigationIds: navIds,
        operationIds: operateIds
      });
      if (status) {
        this.$message.success("功能配置成功");
        this.closeDialog();
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }

  closeDialog() {
    this.$emit("close");
  }
}
