 *
 * @author fqq
 */
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";

import Url from "../../../api";

@Component({
  components: {}
})
export default class packageInfo extends Vue {
  @Prop({ type: String, default: "" })
  id?: string;

  @Prop({ type: Boolean, default: false })
  dialogVisible!: boolean;

  addDialog = false;
  detailInfo: any = {};

  @Watch("dialogVisible")
  handleInfo(newValue: boolean) {
    this.addDialog = newValue;
    if (newValue) {
      this.getDetailInfo();
    }
  }

  closeDialog() {
    this.$emit("close");
  }

  getDetailInfo() {
    this.$http.get(Url.getPackageDetail + "/" + this.id).then((res: any) => {
      if (res.data.status) {
        this.detailInfo = res.data.data;
        if (this.detailInfo.createTime) {
          this.detailInfo.createTime = this.$moment(this.detailInfo.createTime).format("YYYY-MM-DD HH:mm:ss");
        }
        if (this.detailInfo.updateTime) {
          this.detailInfo.updateTime = this.$moment(this.detailInfo.updateTime).format("YYYY-MM-DD HH:mm:ss");
        }
      } else {
        this.$message.error(res.data.message);
      }
    });
  }
}
